.Brand{
    font-size: x-large;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.list{
    color:black;
    font-size: larger;
    margin-left: 25px;
    margin-top: 40px;
   text-decoration:none;
}
.list:active{
    text-decoration: underline 2px cornflowerblue;
}
.filters{
    display: flex;
    flex-direction:column;
    justify-content: left;
}
.main{
    display: flex;
    justify-content: space-around;
}
h4{
    margin-left: 50px;
    margin-right: 50px;
}
.heading{
    display:flex;
    justify-content: space-around;
    margin-left: 50px;
}
.list a{
    text-decoration:none;
}
.topbar{
    height: 100px;
    box-shadow: 2px 0 2px gray;
    background-color: white;
    display: flex;
}
.orders{
    margin-top: 80px;
}
.Page {
    width: 100%;
    box-sizing: border-box;
    padding: 0 8% 4%;
}

.MainHeading {
    margin: 0;
}

.Orders {
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding: 24px 0;
}

.Filter {
    width: 180px;
}

.Filter h3 {
    margin: 0;
}

.FilterOptions {
    display: flex;
    flex-direction: column;
}

.FilterCheckbox {
    margin: 6px 0;
    cursor: pointer;
}

.FilterCheckbox input {
    margin-right: 12px;
}

.OrderTable {
    width: 100%;
    border-collapse: collapse;
}

.OrderTable th, .OrderTable td {
    text-align: left;
    padding: 12px 24px;
    font-size: 14px;
}

.TableRow td {
    background-color: #fff;
    border-bottom: 4px solid #F6F7FB;
}

.names {
    color: rgba(0, 0, 0, 0.8);
    font-weight: 500;
}

.id {
    color: #8c8c8c;
}