.Brand{
    font-size: x-large;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.list{
    color:black;
    font-size: larger;
    margin-left: 25px;
    margin-top: 40px;
   text-decoration:none;
}
.list a{
    text-decoration:none;
}
.topbar{
    height: 100px;
    box-shadow: 2px 0 2px gray;
    background-color: white;
    display: flex;
}
input{
    height: 40px;
    border: none;
    display: block;
    justify-content: center;
    box-shadow: 0px 1px 0px black;
    margin: 8px;
    margin-left: 50px;

}
.loginform{
    height: 300px;
    width:400px;
    box-shadow: 2px 2px 1px  grey;
    border: 1px solid black;
    align-content: center;
    margin-left: 20%;
    margin-top: 10%;
    margin-bottom: 20%;
    background-color: white;
}
.submit{
    background-color:lightseagreen;
    padding: 15px;
    border-radius: 16px;
    color: white;
}